@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap');

/*          <!-- NavBar-->          */
 .navBar{
    display: flex;
    z-index: 10;
    background: #081b29;
    width: 100%;
    height: 75px;
    position:fixed;
    top:0;
    transition: all 1s ease;
    align-items: center;
    font-family: 'Poppins', sans-serif;


}

.leftNB{
    display: flex;
    flex-wrap: wrap;
    width: 29%;
    padding-inline-start: 150px;
    align-items: center;
    }
.leftNB img{
    width: 50px;
    height: 50px;

}
.leftNB p{
    color: #00abf0;
    font-size: large;
    padding-left: 10px;
}

.rightNB{
display:flex;
width: 70%;
justify-content: center;
}

.listaNavBar{
    display: flex;
    flex-wrap: wrap;
    color: aliceblue;    
    
}
.navBar li ,a {
    display: inline;
    flex-wrap: wrap;
    padding-right: 25px;
    list-style: none;
    text-decoration: none;
    color: #00abf0;
    font-size: 16px;
    transition: .5s;
}

.navBar a:hover{
    color: aliceblue;
}

.nav_toggle{
    display: none;
}

