.skills{
    display: flex;
    width: 100%;
    height:100vh;    
    background: #112e42;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to top, #081b29, #112e42);  /* Chrome 10-25, Safari 5.1-6 */        
    background: linear-gradient(to top, #081b29, #112e42); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
    .skillsSec{
        display: flex;
        width: 100%;
        height: calc(100vh - 75px);
        margin-top: 75px;
        overflow: hidden;
        align-items: center;
        flex-direction: column;

            .infoSkills{
                display: inline;
                color: #ededed;
                text-align: center;
                padding-top: 50px;
                
                h1{
                margin-top: 0;
                color: aliceblue;
            text-shadow:0px 0px 50px #00abf0;
            -webkit-text-stroke: .5px #2cc7ff;
            font-size: 26px;
                }
                
                .p{
                    padding: 0;
                }
            }

            
            ul.listaSkills {
                display: flex;
                flex-wrap: wrap;
                padding: 0;
                margin: 0;
                li{
                    display: grid;
                    list-style: none;
                    color: #00abf0;            
                    font-weight: bold;                      
                    align-self: center;
                    justify-items: center;
                    padding: 15px 0px 0px 0px;
                    width: 200px;
                }
                img{
                    display: flex;
                    width: 75px;
                    height: 75px;
                    border-radius: 50%;
                    box-shadow: 20px 20px 60px #bebebe,
                    -20px -20px 60px #00abf0;      
                }
                h2{
                    color: aliceblue;
                    -webkit-text-stroke-width: 0.3px;
                    -webkit-text-stroke-color:  #00abf0;
                    margin-bottom: 0;
                }
            
            }
        
}